import { useState } from "react";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";

import { IDoctorFilter } from "../interfaces/IDoctorFilter";

type DoctorSearchFormProps = {
    searchDoctorsByFilter: (filter: IDoctorFilter) => void;
}

const DoctorSearchForm = ({ searchDoctorsByFilter }: DoctorSearchFormProps) => {
    const [formValues, setFormValues] = useState<IDoctorFilter>({
        id: '',
        firstname: '',
        lastname: '',
        specialty: '',
        specialtyFaculty: '',
        pageSize: 10,
        pageNumber: 1
    });

    const handleInputChange = (e: any) => {
        const { id, value } = e.target;
        setFormValues({
            ...formValues,
            [id]: value,
        });
    }

    function handleSubmit(e: any) {
        e.preventDefault();
        searchDoctorsByFilter(formValues);
    }

    return (
        <>
            <Form className="mb-2" onSubmit={handleSubmit}>
                <Row>
                    <Form.Group className="col-xxs-12 col-xs-12 col-sm-6 col-md-6 col-lg-2"  as={Col} controlId='id'>
                        <Form.Label>Identidad</Form.Label>
                        <Form.Control type='number' onChange={handleInputChange}></Form.Control>
                    </Form.Group>

                    <Form.Group className="col-xs-12 col-sm-6 col-md-6 col-lg-2" as={Col} controlId='firstname'>
                        <Form.Label>Nombre</Form.Label>
                        <Form.Control type='text' onChange={handleInputChange}></Form.Control>
                    </Form.Group>

                    <Form.Group className="col-xs-12 col-sm-6 col-md-6 col-lg-2" as={Col} controlId='lastname'>
                        <Form.Label>Apellido</Form.Label>
                        <Form.Control type='text' onChange={handleInputChange}></Form.Control>
                    </Form.Group>

                    <Form.Group className="col-xs-12 col-sm-6 col-md-6 col-lg-2" as={Col} controlId='specialty'>
                        <Form.Label>Especialidad</Form.Label>
                        <Form.Control type='text' onChange={handleInputChange}></Form.Control>
                    </Form.Group>

                    <Form.Group className="col-xs-12 col-sm-6 col-md-6 col-lg-2" as={Col} controlId='specialtyFaculty'>
                        <Form.Label>Facultad</Form.Label>
                        <Form.Control type='text' onChange={handleInputChange}></Form.Control>
                    </Form.Group>

                    <Col lg={2} xl={2} md="auto" className='mt-4 justify-content'>
                        <Button type="submit" className="mt-1" size="lg">
                            Buscar
                        </Button>
                    </Col>
                </Row>
            </Form>
        </>
    )
}

export default DoctorSearchForm;