import { useState } from "react";
import { getDoctorsClinicalApi } from "../endpoints";
import axios, { AxiosResponse } from "axios";
import { IDoctor } from "../interfaces/IDoctor";
import { IDoctorFilter } from "../interfaces/IDoctorFilter";
import DoctorItems from "./DoctorItems";
import DoctorSearchForm from "./DoctorSearchForm";
import { IMetadata } from "../interfaces/IMetadata";

interface IResponse {
    statusCode: number,
    message: string,
    data: [IDoctor],
    metadata: IMetadata
}

const Doctors = () => {

    const [doctors, setDoctors] = useState<IDoctor[]>([]);
    const [metadata, setMetadata] = useState<IMetadata>({
        currentPage: 1,
        totalPages: 1,
        pageSize: 10,
        totalCount: 0,
        hasPreviousPage: false,
        hasNextPage: false,
        nextPageNumber: 2,
        nextPageUrl: ''
    });
    const [hasResults, setHasResults] = useState(false);
    const [responseMessage, setResponseMessage] = useState('No hay resultados');

    function searchDoctorsByFilter(filter: any) {
        const queryString = Object.keys(filter).map(key => `${encodeURIComponent(key)}=${encodeURIComponent(filter[key])}`).join('&');
        axios.get(`${getDoctorsClinicalApi}?${queryString}`)
            .then((response: AxiosResponse<IResponse>) => {
                console.log(response.data.metadata);
                setDoctors(response.data.data);
                setMetadata(response.data.metadata);
                setHasResults(true);
            })
            .catch((error) => {
                console.error(error);
            });
    }

    return (
        <>
            <h2>Doctores</h2>
            <DoctorSearchForm searchDoctorsByFilter={searchDoctorsByFilter} />
            <DoctorItems hasResults={hasResults} doctors={doctors} metadata={metadata} responseMessage={responseMessage} />
        </>
    )
}

export default Doctors;