import { useState, memo } from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import Alert from 'react-bootstrap/Alert';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { IDoctor } from '../interfaces/IDoctor';
import CreateDoctorForm from './CreateDoctorForm';
import CustomPagination from './CustomPagination';
import Container from 'react-bootstrap/esm/Container';
import { IMetadata } from '../interfaces/IMetadata';

type DoctorItemsProps = {
    hasResults: boolean;
    doctors: IDoctor[];
    metadata: IMetadata;
    responseMessage: string;
}

const DoctorItems = memo(({ hasResults, doctors, metadata, responseMessage }: DoctorItemsProps) => {

    const [showMessage, setShowMessage] = useState(true);
    const [showModal, setShowModal] = useState(false);
    const handleCloseModal = () => setShowModal(false);
    const handleShowModal = () => setShowModal(true);

    return (
        <>
            <Button className='btn-sm mb-2' variant='primary' onClick={handleShowModal}>
                Agregar
            </Button>
            <Row style={{ maxHeight: '70vh', overflowY: 'auto' }} xs={1} sm={2} md={4} lg={4} className='g-1'>
                {
                    hasResults ? doctors.map((doc: IDoctor, idx: any) => (
                        <Col id={'col-' + idx} key={'col-' + idx} style={{ marginBottom: '1em' }}>
                            <Card style={{ width: '15rem' }}>
                                <Card.Img variant="top" src="https://st3.depositphotos.com/12982378/17554/i/1600/depositphotos_175549982-stock-photo-doctor.jpg" />
                                <Card.Body>
                                    <Card.Title>{doc.firstname + " " + doc.lastname}</Card.Title>
                                    <Card.Text>
                                        {doc.specialty}
                                    </Card.Text>
                                </Card.Body>
                                <ListGroup className="list-group-flush">
                                    <ListGroup.Item title='Facultad'>{doc.specialtyFaculty}</ListGroup.Item>
                                </ListGroup>
                                <Card.Body>
                                    <Card.Link href={"mailto:" + doc.email + "?subject=Hola Dr(a) " + doc.firstname}>{doc.email}</Card.Link>
                                </Card.Body>
                            </Card>
                        </Col>
                    )) :
                        <Col xs={12} sm={12} md={12} lg={12} >
                            <Alert className='justify-content-md-center' variant='danger' onClose={() => setShowMessage(false)} dismissible>
                                <Alert.Heading style={{ fontSize: '1em' }} className='text-center '>{responseMessage}</Alert.Heading>
                            </Alert>
                        </Col>
                }
            </Row >
            <Row className='justify-content-md-center'>
                <Col xs={1} sm={1} md={1} >
                    <CustomPagination metadata={metadata} />
                </Col>
            </Row>

            <Modal
                show={showModal}
                onHide={handleCloseModal}
                size='lg'
            >
                <Modal.Header closeButton>
                    <Modal.Title>Crear Doctor</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <CreateDoctorForm />
                </Modal.Body>
                {/* <Modal.Footer>
                    <Button variant='secondary' onClick={handleCloseModal}>
                        Cerrar
                    </Button>
                    <Button variant='primary' onClick={handleCloseModal}>
                        Guardar
                    </Button>
                </Modal.Footer> */}
            </Modal>
        </>
    )
});

export default DoctorItems;