import { useState } from "react";
import axios, { AxiosResponse } from "axios";
import * as formik from "formik";
import * as yup from "yup";
import { createDoctorClinicalApi } from "../endpoints";

import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Alert from "react-bootstrap/Alert";
import { STATUS_CODES } from "http";


interface Doctor {
    id: string;
    firstname: string;
    lastname: string;
    dateOfBirth: string;
    gender: string
    email: string
    cellPhoneNumber: string;
    specialty: string;
    specialtyFaculty: string;
    image: string;
}

interface Error {
    key: string;
    value: string;
}

const CreateDoctorForm = () => {

    const [show, setShow] = useState(false);
    const [messages, setMessages] = useState<Error[]>([]);
    const [messageVariant, setMessageVariant] = useState('');

    function handleSubmit(data: Doctor) {
        console.log("LLega")
        console.log(data);


        axios.post(createDoctorClinicalApi, data).
            then((response: AxiosResponse<any>) => {
                console.log("Response: ", response);

                switch (response.data.statusCode) {
                    case 200:
                        setMessageVariant('success');
                        setShow(true);
                        let messages = new Array();
                        messages.push({
                            key: '200',
                            value: response.data.data
                        });
                        setMessages(messages);
                        break;

                    default:
                        break;
                }
            }).catch((error) => {
                let errorData = error.response.data;
                console.log("Error data: ", errorData);

                let status = error.response.data.status;
                console.log("status", status);
                let uiErrors = new Array();

                switch (status) {
                    case 400:
                        let errors = errorData.errors;
                        console.log("Errors", errors);
                        errors.forEach((err: any) => {
                            uiErrors.push({
                                key: err.propertyName,
                                value: err.errorMessage
                            });
                        });
                        break;
                    case 500:

                        break;

                    default:
                        break;
                }
                // let errors = errorData.errors;
                // let uiErrors = new Array();
                // if (status === undefined) {
                //     status = errorData.errors[0].status;
                //     errors.forEach((err: any) => {
                //         uiErrors.push({
                //             key: err.title,
                //             value: err.detail
                //         });
                //     });
                // } else {
                //     for (let key in errors) {
                //         uiErrors.push({
                //             key: key,
                //             value: errors[key][0]
                //         });
                //     }
                // }
                setMessageVariant('danger');
                setShow(true);
                setMessages(uiErrors);
            })
    }

    const { Formik } = formik;

    const schema: yup.ObjectSchema<Doctor> = yup.object({
        id: yup.string()
            .required('campo requerido*'),
        firstname: yup.string()
            .required('campo requerido*')
            .min(4, "Mnimo 4 caracteres")
            .max(30, "Mximo 30 caracteres"),
        lastname: yup.string()
            .required('campo requerido*')
            .min(4, "Mnimo 4 caracteres")
            .max(30, "Mximo 30 caracteres"),
        dateOfBirth: yup.string()
            .required('campo requerido*'),
        gender: yup.string()
            .required('campo requerido*'),
        cellPhoneNumber: yup.string()
            .required('campo requerido*'),
        email: yup.string()
            .required('campo requerido*')
            .email("Correo electrnico invlido"),
        specialty: yup.string()
            .required('campo requerido*'),
        specialtyFaculty: yup.string()
            .required('campo requerido*'),
        image: yup.string()
            .required('campo requerido*')
    });

    return (
        <>
            <Formik
                validationSchema={schema}
                onSubmit={handleSubmit}
                initialValues={{
                    id: '',
                    firstname: '',
                    lastname: '',
                    dateOfBirth: '',
                    gender: '',
                    email: '',
                    cellPhoneNumber: '',
                    specialty: '',
                    specialtyFaculty: '',
                    image: '',
                }}
            >
                {({ handleSubmit, handleChange, values, touched, errors }) => (
                    <Form noValidate onSubmit={handleSubmit}>
                        <Row>
                            <Form.Group as={Col} md="4" controlId="doctorId">
                                <Form.Label>Doc. Identidad</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="ID"
                                    name="id"
                                    value={values.id}
                                    onChange={handleChange}
                                    isValid={touched.id && !errors.id}
                                    isInvalid={touched.id && !!errors.id}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.id}
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="firstname">
                                <Form.Label>Nombre</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Nombre"
                                    name="firstname"
                                    value={values.firstname}
                                    onChange={handleChange}
                                    isValid={touched.firstname && !errors.firstname}
                                    isInvalid={touched.firstname && !!errors.firstname}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.firstname}
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="lastname">
                                <Form.Label>Apellido</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Apellido"
                                    name="lastname"
                                    value={values.lastname}
                                    onChange={handleChange}
                                    isValid={touched.lastname && !errors.lastname}
                                    isInvalid={touched.lastname && !!errors.lastname}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.lastname}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>
                        <Row>
                            <Form.Group as={Col} md="4" controlId="dateOfBirth">
                                <Form.Label>Fecha Nacimiento</Form.Label>
                                <Form.Control
                                    type="date"
                                    placeholder=""
                                    name="dateOfBirth"
                                    value={values.dateOfBirth}
                                    onChange={handleChange}
                                    isValid={touched.dateOfBirth && !errors.dateOfBirth}
                                    isInvalid={touched.dateOfBirth && !!errors.dateOfBirth}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.dateOfBirth}
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="gender">
                                <Form.Label>Gnero</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Gnero"
                                    name="gender"
                                    value={values.gender}
                                    onChange={handleChange}
                                    isValid={touched.gender && !errors.gender}
                                    isInvalid={touched.gender && !!errors.gender}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.gender}
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="email">
                                <Form.Label>Email</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Email"
                                    name="email"
                                    value={values.email}
                                    onChange={handleChange}
                                    isValid={touched.email && !errors.email}
                                    isInvalid={touched.email && !!errors.email}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.email}
                                </Form.Control.Feedback>
                            </Form.Group>

                        </Row>
                        <Row>
                            <Form.Group as={Col} md="4" controlId="cellPhoneNumber">
                                <Form.Label>Telfono</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Telfono"
                                    name="cellPhoneNumber"
                                    value={values.cellPhoneNumber}
                                    onChange={handleChange}
                                    isValid={touched.cellPhoneNumber && !errors.cellPhoneNumber}
                                    isInvalid={touched.cellPhoneNumber && !!errors.cellPhoneNumber}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.cellPhoneNumber}
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="specialty">
                                <Form.Label>Especialidad</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Especialidad"
                                    name="specialty"
                                    value={values.specialty}
                                    onChange={handleChange}
                                    isValid={touched.specialty && !errors.specialty}
                                    isInvalid={touched.specialty && !!errors.specialty}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.specialty}
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="specialtyFaculty">
                                <Form.Label>Facultad</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Facultad"
                                    name="specialtyFaculty"
                                    value={values.specialtyFaculty}
                                    onChange={handleChange}
                                    isValid={touched.specialtyFaculty && !errors.specialtyFaculty}
                                    isInvalid={touched.specialtyFaculty && !!errors.specialtyFaculty}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.specialtyFaculty}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>
                        <Row>
                            <Form.Group as={Col} md="4" controlId="image">
                                <Form.Label>Imagen</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Imagen"
                                    name="image"
                                    value={values.image}
                                    onChange={handleChange}
                                    isValid={touched.image && !errors.image}
                                    isInvalid={touched.image && !!errors.image}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.image}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>
                        <Row className="m-0 mt-2">
                            <Button type="submit">Guardar</Button>
                        </Row>
                    </Form>
                )}
            </Formik>
            <br />
            {
                show ?
                    <Alert className='m-0 p-3 pt-3' variant={messageVariant} onClose={() => setShow(false)} dismissible>
                        {messages.map((e: any, idx: any) => (
                            <li className="m-2">{e.key + ": " + e.value}</li>
                        ))}
                    </Alert>
                    : <br />
            }

        </>
    )
}

export default CreateDoctorForm;