import Pagination from "react-bootstrap/Pagination";
import { IMetadata } from "../interfaces/IMetadata";
import { useEffect, useState } from "react";

type MyProps = {
    metadata: IMetadata
};

const CustomPagination = ({metadata}: MyProps) => {
    console.log("llama al componente pagination")
    const [active, setActive] = useState(metadata.currentPage);
    const items = Array();
    //setActive(currentPage);
    //useEffect(() => {

    for (let page = 1; page <= metadata.totalPages; page++) {
        items.push(
            <Pagination.Item onClick={() => {
                console.log("ir a la page ", page)
                setActive(page);
            }} key={page} active={page === active}>
                {page}
            </Pagination.Item>
        )
    }

    //}, [active]);

    return (
        <>
            <Pagination size="sm">
                <Pagination.First />
                <Pagination.Prev />
                {items}
                <Pagination.Next />
                <Pagination.Last />
            </Pagination>
        </>
    )
}

export default CustomPagination;